<template>
    <div id="show-qr">
        <div id="header">
            <img id="logo" :src="require('../assets/qrlogo.svg')" alt=""/>
            <div id="title">Código QR</div>
        </div>
        
        <div id="details">
            <canvas id="qrcode" ref="qrcode"></canvas>
            <div id="data">
                <Label class="input" type="text" :icon="require('../assets/commerce.svg')" label="Comercio" v-model="this.$route.params.commerce"/>
                <Label class="input" type="text" :icon="require('../assets/money.svg')" label="Monto" v-model="'$ ' + (this.$route.params.amount / 100).toFixed(2)"/>
                <Label class="input" type="text" :icon="require('../assets/smallbag.svg')" label="Concepto" v-model="this.$route.params.concept"/>
                <Label class="input" type="text" :icon="require('../assets/ref.svg')" label="Referencia" v-model="this.$route.params.reference"/>
            </div>
        </div>
        
        <div @click="$router.back();" id="button">Cerrar</div>
    </div>
</template>

<script>
import QRCode from 'qrcode'
import Label from '@/components/Label'

export default {
    name: 'ShowQR',
    data() {
        return { 
        }
    },
    components: {
        Label
    },
    mounted() {
        let code = this.$refs.qrcode;
        console.log(`{"commerce":"${this.$route.params.commerce}","amount":${this.$route.params.amount},"concept":"${this.$route.params.concept}"}`);
        
        QRCode.toCanvas(code, `{"commerce":"${this.$route.params.commerce}","amount":${this.$route.params.amount},"concept":"${this.$route.params.concept}"}`, { errorCorrectionLevel: 'M' }, (error) => {
            if (error) console.error(error)
            console.log('success!');
        })
    },
    methods: {
        finish() {
            console.log('close');
            this.$router.replace({name: 'main_menu'});
        }
    }
}
</script>

<style scoped>

#show-qr {
    height: 100%;
    width: 100%;
    display: grid;
    grid-template-rows: 42px 80% 10%;
    align-items: center;
}

#header {
    width: 100%;
    height: 100%;
    background: linear-gradient(135deg, #67fed7 0%,#0fabf9 100%);
    position: relative;
}

#title {
    position: absolute;
    top: 20%;
    left: 15%;
    width: 70%;
    text-align: left;
    font-size: 20px;
    color: white;
}

#close {
    position: absolute;
    left: 90%;
    top: 10%;
}

#logo {
    position: absolute;
    top: 8%;
    left: 2%;
    width: 32px;
    height: 32px;
}

#details {
    position: relative;
    margin: 5%;
    height: 100%;
}

#data {
    position: absolute;
    top: 40%;
    width: 100%;
}
#qrcode {
    position: absolute;
    top: 25%;
    left: 50%;
    transform: translate(-50%, -50%);
}
#button {
    text-align: center;
    background: linear-gradient(135deg, #67fed7 0%,#0fabf9 100%);
    margin-top: 5%; 
    margin-left: 25%;
    margin-right: 25%;
    border-radius: 20px;
    font-size: 20px;
    color: white;
    box-shadow: 0 3px 6px rgba(57,211,232,0.16), 0 3px 6px rgba(57,211,232,0.23);
    height: 40px;
    line-height: 40px;
}


</style>
