<template>
    <div class="code-register">
        <div id="header">
            <img id="logo" :src="require('../assets/bag.svg')" alt=""/>
            <div id="content">{{ content }}</div>
        </div>
        <div id="details">
            <Input class="input" type="text" :icon="require('../assets/commerce.svg')" label="Código" v-model="codr"/>
        </div>
        <div id="buttons">
            <div class="button" @click="register">Continuar</div>
        </div>
        
        <Dialog v-if="modal" :title="dialog_title" :content="dialog_content" :type="process" />
    </div>
</template>

<script>
import Input from '@/components/Input'
import Dialog from '@/components/Dialog'
import firebase from 'firebase'

export default {
    name: 'CodrRegister',
    data: function() {
        return {
            codr: '',
            title: 'Atrás',
            content:'Introduzca el código recibido',
            modal: false,
            process: '',
            dialog_title: '',
            dialog_content: ''
        }
    },
    components: {
        Input,
        Dialog
    },
    mounted: function() {
        let uid = firebase.auth().currentUser.uid;
        let db = firebase.firestore();
        let user_doc = db.collection("users").doc(uid);
        user_doc.get().then((doc) => {
            if (doc.exists) {
                this.user = doc.data();
                console.log(this.user);
                if(this.user.cod_r) {
                    this.$router.replace({name: 'main_menu'});
                } else {
                    
                }
            } else {
                console.log("No such document!");
            }
        }).catch((error) => {
            console.log("Error getting document:", error);
        });
    },
    methods: {
        register() {
            this.modal = true;
            this.process = 'success';
            this.dialog_title = 'Éxito';
            this.dialog_content = 'Su teléfono ha sido registrado';

            let uid = firebase.auth().currentUser.uid;
            let db = firebase.firestore();
            let user_doc = db.collection("users").doc(uid);
            user_doc.update({
                cod_r: this.codr
            });

            setTimeout(() => this.$router.replace({name: 'main_menu'}), 2000);

        }
    }
}
</script>

<style scoped>
.code-register {
    height: 100%;
    width: 100%;
    display: grid;
    grid-template-rows: 40% 45% 15%;
    align-items: center;
}

#header {
    width: 100;
    height: 100%;
    background: linear-gradient(135deg, #67fed7 0%,#0fabf9 100%);
    position: relative;
}

#title {
    position: absolute;
    top: 5%;
    left: 8%;
    width: 20%;
    text-align: center;
    font-size: 20px;
    color: white;
}

#content {
    position: absolute;
    top: 70%;
    width: 70%;
    margin-left: 15%;
    margin-right: 15%;
    text-align: center;
    font-size: 22px;
    color: white;
}

#close {
    position: absolute;
    left: 90%;
    top: 3%;
}

#back {
    position: absolute;
    left: 3%;
    top: 3%;
}

#logo {
    position: absolute;
    top: 40%;
    left: 50%;
    transform: translate(-50%, -50%);
}

#details {
    margin: 5%;
    align-self: center;
}

.button {
    text-align: center;
    background: linear-gradient(135deg, #67fed7 0%,#0fabf9 100%);
    margin-left: 25%;
    margin-right: 25%;
    border-radius: 20px;
    font-size: 22px;
    color: white;
    box-shadow: 0 3px 6px rgba(57,211,232,0.16), 0 3px 6px rgba(57,211,232,0.23);
    height: 40px;
    line-height: 40px;
}

</style>
