<template>
    <div class="generate-qr">
        <div id="header">
            <img id="close" :src="require('../assets/close.svg')" alt="" @click="$router.back();"/>
            <img id="logo" :src="require('../assets/prod_to_qr.svg')" alt=""/>
            <div id="content">{{ content }}</div>
        </div>
        <div id="details">
            <Input class="input" type="number" :icon="require('../assets/money.svg')" label="Monto (centavos)" v-model="amount"/>
            <Input class="input" type="text" :icon="require('../assets/smallbag.svg')" label="Concepto" v-model="concept"/>
            <Input class="input" type="text" :icon="require('../assets/ref.svg')" label="Referencia" v-model="reference"/>
        </div>
        <div id="buttons">
            <div class="button" @click="$router.replace({name: 'show_qr', params: {commerce: 'Patos', amount: amount, concept: concept, reference: reference}});">Generar QR</div>
        </div>

        <Dialog v-if="modal" :title="dialog_title" :content="dialog_content" :type="process" />

    </div>
</template>

<script>
import Input from '@/components/Input'
import Dialog from '@/components/Dialog'
import firebase from 'firebase'

export default {
    name: 'GenerateQR',
    data: function() {
        return {
            amount:'',
            concept: '',
            reference: '',
            title: 'Atrás',
            content:'Introduzca los datos para crear un código QR',
            canvas_tag: {},
            canvas: null,
            modal: false,
            process: '',
            dialog_title: '',
            dialog_content: ''
        }
    },
    components: {
        Input,
        Dialog
    },
    mounted: function() {

    },
    methods: {
        register() {
            this.modal = true;
            this.process = 'progress';
            this.dialog_title = 'Registrando';
            this.dialog_content = 'Por favor espere un momento';

            let message = `d={
                "nc" : ${this.phone},
                "idH" : "3213213213213213213213213213213213213213213213213213213213213213-${this.commerce}",
                "ia" : {
                    "so": "${this.os_name}",
                    "vSO" : "${this.os_version}",
                    "fab" : "${this.factory}",
                    "mod" : "${this.model}"
                    }
            }`

        },
        finish() {
            
            setTimeout(() => this.$router.replace({name: 'show_qr'}), 3000);
        },
        close() {
            
        }
    }
}
</script>

<style scoped>

.generate-qr {
    height: 100%;
    width: 100%;
    display: grid;
    grid-template-rows: 42px 55% 30%;
    align-items: center;
}

#header {
    width: 100;
    height: 100%;
    background: linear-gradient(135deg, #67fed7 0%,#0fabf9 100%);
    position: relative;
}

#title {
    position: absolute;
    top: 5%;
    left: 8%;
    width: 20%;
    text-align: center;
    font-size: 20px;
    color: white;
}

#content {
    position: absolute;
    top: 12px;
    width: 60%;
    left: 15%;
    text-align: center;
    font-size: 22px;
    color: white;
}

#close {
    position: absolute;
    left: 90%;
    top: 10%;
}

#back {
    position: absolute;
    left: 3%;
    top: 3%;
}

#logo {
    width: 64px;
    height: 32px;
    margin: 5px;
}

#details {
    margin: 5%;
    height: 70%;
    align-self: center;
    align-items: center;
    display: grid;
}


.button {
    text-align: center;
    background: linear-gradient(135deg, #67fed7 0%,#0fabf9 100%);
    margin-left: 25%;
    margin-right: 25%;
    border-radius: 20px;
    font-size: 22px;
    color: white;
    box-shadow: 0 3px 6px rgba(57,211,232,0.16), 0 3px 6px rgba(57,211,232,0.23);
    height: 40px;
    line-height: 40px;
}


#dialog-background {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: #00000077;
}

#dialog-window {
    position: absolute;
    background-color: white;
    border-radius: 5px;
    width: 70%;
    height: 50%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

#dialog-title {
    position: absolute;
    top: 55%;
    width: 80%;
    margin-left: 10%;
    font-size: 25px;
    text-align: center;
    font-weight: bold;
}

#dialog-content {
    position: absolute;
    top: 70%;
    width: 80%;
    margin-left: 10%;
    font-size: 20px;
    text-align: center;
}

#spinner, #success {
    position: absolute;
    width: 50%;
    height: 50%;
    top: 30%;
    left: 50%;
    transform: translate(-50%, -50%);
}

@media (min-height: 600px) {
    .generate-qr {
        grid-template-rows: 40% 45% 15%;
    }
    #logo {
        position: absolute;
        top: 40%;
        left: 50%;
        width: auto;
        height: auto;
        transform: translate(-50%, -50%);
    }

    #content {
        position: absolute;
        top: 70%;
        left: 0;
        width: 70%;
        margin-left: 15%;
        margin-right: 15%;
        text-align: center;
    }
    #close {
        top: 3%;
    }
}

</style>
