import Vue from 'vue'
import Router from 'vue-router'
import Signin from './views/Signin.vue'
import MainMenu from './views/MainMenu.vue'
import ScanQR from './views/ScanQR.vue'
import PaymentDetails from './views/PaymentDetails.vue'
import PhoneRegister from './views/PhoneRegister.vue'
import CodrRegister from './views/CodrRegister.vue'
import GenerateQR from './views/GenerateQR.vue'
import ShowQR from './views/ShowQR.vue'
import QRList from './views/QRList.vue'
import firebase from 'firebase'

Vue.use(Router)

const router =  new Router({
  mode: 'history',
  base: 'QRPayment',
  routes: [
    {
      path: '*',
      component: Signin
    },
    {
      path: '/',
      name: 'signin',
      component: Signin
    },
    {
      path: '/main_menu',
      name: 'main_menu',
      component: MainMenu,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: '/scan_code',
      name: 'scan_code',
      component: ScanQR,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: '/payment_details',
      name: 'payment_details',
      component: PaymentDetails,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: '/qr_list',
      name: 'qr_list',
      component: QRList,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: '/generate_qr',
      name: 'generate_qr',
      component: GenerateQR,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: '/show_qr',
      name: 'show_qr',
      component: ShowQR,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: '/phone_register',
      name: 'phone_register',
      component: PhoneRegister,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: '/codr_register',
      name: 'codr_register',
      component: CodrRegister,
      meta: {
        requiresAuth: true
      }
    }
  ]
})

router.beforeEach((to, from, next) => {
  const currentUser = firebase.auth().currentUser;
  const requiresAuth = to.matched.some(record => record.meta.requiresAuth);

  if(requiresAuth && !currentUser) next('signin');
  else if(!requiresAuth && currentUser) next('main_menu');
  else next();
})

export default router;