<template>
    <div id="main-menu">
        <div id="header">
            <img id="menu" :src="require('../assets/menu.svg')" alt="" />
            <img id="logout" :src="require('../assets/logout.svg')" alt="" @click="logout"/>
            <img id="logo" :src="require('../assets/avatar.svg')" alt=""/>
            <div id="title">{{ user? user.display_name: 'Loading' }}</div>
        </div>
        <div id="options">
            <MenuItem
                v-for="(item, index) in list"
                :key="index"
                :icon="item.icon"
                :title="item.title"
                :description="item.description"
                :color="item.color"
                :action="item.action"
                :idx="list.length - index"
            />
        </div>
    </div>
</template>

<script>
import MenuItem from '@/components/MenuItem'
import MenuHeader from '@/components/MenuHeader'
import firebase from 'firebase'

export default {
    name: 'MainMenu',
    components: {
        MenuItem,
        MenuHeader
    },
    data: function() {
        return {
            list: [],
            user: null
        }
    },
    mounted() {
        let uid = firebase.auth().currentUser.uid;
        let db = firebase.firestore();
        let user_doc = db.collection("users").doc(uid);
        user_doc.get().then((doc) => {
            if (doc.exists) {
                this.user = doc.data();
                console.log(this.user);
                if(!this.user.phone) {
                    this.$router.replace({name: 'phone_register'});
                } else if(!this.user.cod_r) {
                    this.$router.replace({name: 'codr_register'});
                } else {
                    this.list = [ 
                        {
                            icon: require('../assets/pay.svg'),
                            title: 'Pagar',
                            description: 'Leer un código QR para pagar',
                            action: 'scan_code'
                        },
                        {
                            icon: require('../assets/charge.svg'),
                            title: 'Cobrar',
                            description: 'Generar un código QR para pagar',
                            action: 'generate_qr'
                        },
                        {
                            icon: require('../assets/wallet.svg'),
                            title: 'Billetera',
                            description: 'Administración de métodos de pago',
                            action: 'transaction_list'
                        },
                        {
                            icon: require('../assets/transactions.svg'),
                            title: 'Transacciones',
                            description: 'Historial de transacciones realizadas',
                            action: 'settings'
                        }
                    ]
                }
            } else {
                console.log("No such document!");
            }
        }).catch((error) => {
            console.log("Error getting document:", error);
        });
    },
    methods: {
        logout() {
            firebase.auth().signOut().then(() => {
                this.$router.replace({name: 'signin'});
            }).catch((error) => {
                this.$router.replace({name: 'signin'});
            });
        }
    }
}
</script>

<style scoped>
#main-menu {
    width: 100%;
    height: 100%;
    display: grid;
    grid-template-rows: 40% 60%;
    
}

#header {
    width: 100;
    height: 100%;
    background: linear-gradient(135deg, #67fed7 0%,#0fabf9 100%);
    position: relative;
}

#title {
    position: absolute;
    top: 70%;
    left: 10%;
    width: 80%;
    text-align: center;
    font-size: 25px;
    color: white;
}

#logo {
    position: absolute;
    top: 40%;
    left: 50%;
    transform: translate(-50%, -50%);
}

#options {
    display: grid;
    grid-template-rows: repeat(20%);
    margin: 5%;
}

#logout {
    position: absolute;
    left: 88%;
    top: 3%;
}

#menu {
    position: absolute;
    left: 3%;
    top: 3%;
}

</style>
