<template>
    <div class="phone-register">
        <div id="header">
            <img id="logo" :src="require('../assets/record.svg')" alt=""/>
            <div id="content">{{ content }}</div>
        </div>
        <div id="details">
            <Input class="input" type="text" :icon="require('../assets/phone.svg')" label="Teléfono" v-model="phone"/>
            <Input class="input" type="text" :icon="require('../assets/commerce.svg')" label="Comercio (sin espacios)" v-model="commerce"/>
            <Input class="input" type="text" :icon="require('../assets/os.svg')" label="Sistema Operativo" v-model="os_name"/>
            <Input class="input" type="text" :icon="require('../assets/smartphone.svg')" label="Fabricante" v-model="factory"/>
            <Input class="input" type="text" :icon="require('../assets/version.svg')" label="Versión del sistema" v-model="os_version"/>
            <Input class="input" type="text" :icon="require('../assets/model.svg')" label="Modelo" v-model="model"/>
        </div>
        <div id="buttons">
            <div class="button" @click="register">Registrar</div>
        </div>

        <Dialog v-if="modal" :title="dialog_title" :content="dialog_content" :type="process" />

    </div>
</template>

<script>
import Input from '@/components/Input'
import Dialog from '@/components/Dialog'
import firebase from 'firebase'

export default {
    name: 'PhoneRegister',
    data: function() {
        return {
            phone:'',
            commerce: '',
            os_name: '',
            os_version: '',
            factory: '',
            model: '',
            title: 'Atrás',
            content:'Registre su teléfono para realizar pagos y cobros',
            canvas_tag: {},
            canvas: null,
            modal: false,
            process: '',
            dialog_title: '',
            dialog_content: ''
        }
    },
    components: {
        Input,
        Dialog
    },
    mounted: function() {
        let uid = firebase.auth().currentUser.uid;
        let db = firebase.firestore();
        let user_doc = db.collection("users").doc(uid);
        user_doc.get().then((doc) => {
            if (doc.exists) {
                this.user = doc.data();
                console.log(this.user);
                if(this.user.phone) {
                    this.$router.replace({name: 'codr_register'});
                }
            } else {
                console.log("No such document!");
            }
        }).catch((error) => {
            console.log("Error getting document:", error);
        });
    },
    methods: {
        register() {
            this.modal = true;
            this.process = 'progress';
            this.dialog_title = 'Registrando';
            this.dialog_content = 'Por favor espere un momento';

            let message = `d={
                "nc" : ${this.phone},
                "idH" : "3213213213213213213213213213213213213213213213213213213213213213-${this.commerce}",
                "ia" : {
                    "so": "${this.os_name}",
                    "vSO" : "${this.os_version}",
                    "fab" : "${this.factory}",
                    "mod" : "${this.model}"
                    }
            }`

            let url = 'https://www.banxico.org.mx/pagospei-beta/registroInicial';

            // The parameters we are gonna pass to the fetch function
            let fetchData = { 
                method: 'POST', 
                body: message
            }

            fetch(url, fetchData)
            .then(response => {
                console.log(response);
                this.finish();
            })
            .catch((error) => {
                console.log(error);
                this.finish();
            })
        },
        finish() {
            this.process = 'success';
            this.dialog_title = 'Éxito';
            this.dialog_content = 'Registro exitoso';
            
            let uid = firebase.auth().currentUser.uid;
            let db = firebase.firestore();
            let user_doc = db.collection("users").doc(uid);
            user_doc.update({
                phone: this.phone,
                commerce: this.commerce
            });

            setTimeout(() => this.$router.replace({name: 'codr_register'}), 3000);
        },
        close() {
            
        }
    }
}
</script>

<style scoped>

.phone-register {
    height: 100%;
    width: 100%;
    display: grid;
    grid-template-rows: 42px 55% 30%;
    align-items: center;
}

#header {
    width: 100;
    height: 100%;
    background: linear-gradient(135deg, #67fed7 0%,#0fabf9 100%);
    position: relative;
}

#title {
    position: absolute;
    top: 5%;
    left: 8%;
    width: 20%;
    text-align: center;
    font-size: 20px;
    color: white;
}

#content {
    position: absolute;
    top: 15px;
    width: 60%;
    left: 10%;
    text-align: center;
    font-size: 22px;
    color: white;
}

#close {
    position: absolute;
    left: 90%;
    top: 3%;
}

#back {
    position: absolute;
    left: 3%;
    top: 3%;
}

#logo {
    width: 32px;
    height: 32px;
    margin: 5px;
}

#details {
    margin: 5%;
    margin-right: 0%;
    display: grid;
    grid-template-columns: 45% 45%;
    align-self: center;
    grid-gap: 5%;
    height: 80%;
}

.input:nth-child(n + 3) {
    width: 94%;
}

.input:nth-child(1), .input:nth-child(2) {
    grid-column: 1 / 3;
}

.button {
    text-align: center;
    background: linear-gradient(135deg, #67fed7 0%,#0fabf9 100%);
    margin-left: 25%;
    margin-right: 25%;
    border-radius: 20px;
    font-size: 22px;
    color: white;
    box-shadow: 0 3px 6px rgba(57,211,232,0.16), 0 3px 6px rgba(57,211,232,0.23);
    height: 40px;
    line-height: 40px;
}


#dialog-background {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: #00000077;
}

#dialog-window {
    position: absolute;
    background-color: white;
    border-radius: 5px;
    width: 70%;
    height: 50%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

#dialog-title {
    position: absolute;
    top: 55%;
    width: 80%;
    margin-left: 10%;
    font-size: 25px;
    text-align: center;
    font-weight: bold;
}

#dialog-content {
    position: absolute;
    top: 70%;
    width: 80%;
    margin-left: 10%;
    font-size: 20px;
    text-align: center;
}

#spinner, #success {
    position: absolute;
    width: 50%;
    height: 50%;
    top: 30%;
    left: 50%;
    transform: translate(-50%, -50%);
}

@media (min-height: 600px) {
    .phone-register {
        grid-template-rows: 40% 45% 15%;
    }
    #logo {
        position: absolute;
        top: 40%;
        left: 50%;
        width: auto;
        height: auto;
        transform: translate(-50%, -50%);
    }

    #content {
        position: absolute;
        top: 70%;
        left: 0;
        width: 70%;
        margin-left: 15%;
        margin-right: 15%;
        text-align: center;
    }
}

</style>
